<template>
  <div class="store-test">
    <h1>{{ msg }}</h1>
    <div>
      <div class="user-info">姓名：{{userInfo.username}}</div>
      <div class="user-info">年龄：{{userInfo.age}}</div>
      <div class="user-info">地址：{{userInfo.address}}</div>
    </div>

    <button @click="upadteUserInfo()">修改个人信息</button>

    <div class="user-info">
      姓名：<input v-model="userInfo.username">
    </div>
    <div class="user-info">
      年龄：<input v-model="userInfo.age">
    </div>
    <div class="user-info">
      地址：<input v-model="userInfo.address">
    </div>
  </div>
</template>

<script>

export default {
  name: 'Store',
  computed: {
    count () {
      return this.$store.state.count
    }
  },
  data () {
    return {
      userInfo: {},
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted () {
    this.userInfo = this.$store.getters.getUserInfo
    console.log(JSON.stringify(this.$store.getters.getUserInfo))
  },
  methods: {
    upadteUserInfo: function (index) {
      // 调用vuex的mutations设置userInfo的值
      this.$store.commit('userInfo', this.userInfo)
      console.log(JSON.stringify(this.$store.getters.getUserInfo))
      // 返回到首页
      // this.$router.push('/')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .user-info {
    display: flex;
    justify-content: space-between;
    padding: 5px 10px
  }
</style>



